body {
  background-color: #09090a;
  color: #fff;

  header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 48px;

    position: fixed;
    width: 100%;
    background-color: #09090a;

    button {
      padding: 16px 24px;
      border-radius: 8px;
      border: 1px solid #8b5cf6;
      background-color: transparent;
      color: #fff;
      margin-right: 70px;

      display: flex;
      align-items: center;
      gap: 12px;

      font-family: 'Inter';
      font-weight: 600;
      font-size: 16px;
      line-height: 125%;
      
    }
  }

  #form-habits {
    display: flex;
    // gap: 10px;
    padding: 182px 48px 48px;
    width: fit-content;


    .habits {
      margin-top: 65px;
      display: flex;
      flex-direction: column;
      gap: 24px;

      .habit {
        // border: 1px solid red;

        width: 64px;
        height: 64px;

        font-size: 32px;

        display: flex;
        align-items: center;
        justify-content: center;
      }
    }

    .days {
      display: flex;
      gap: 48px;
      margin-left: 32px;

      .day {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 24px;

        div {
          margin-bottom: 8px;

          font-family: 'Roboto Mono';
          font-size: 20px;
          line-height: 125%;
          color: #a1a1aa;
          // text-align: center;
        }
      }
    }

    input {
      appearance: none;
      -webkit-appearance: none;
  
      width: 64px;
      height: 64px;
  
      border: 1px solid #27272a;
      border-radius: 8px;
  
      background: #18181b;
  
      // pseudo-selector
      &:checked {
        background: #8b5cf6;
        border: 2px solid #a78bda;
      }
    }
  }

  @media (max-width: 570px) {
    button {
      div {
        display: none;
      }
    }
  }


}

